* {
  margin: 0px;
  padding: 0px;
}

body {
  color: #333;
  font-family: "맑은 고딕", sans-serif, arial;
  font-size: 16px;
}

.header {
  position: fixed;
  top:0px;left:0px;
  z-index:999;
  width:100%;
  background: rgb(127, 72, 255);
  text-align: center;
  padding: 20px;
}

.header>h1 {
  color: #fff;
  font-size: 30px;
}

.footer {
  text-align: center;
  line-height: 40px;
  background: #333;
}

.footer>address {
  color: #fff;
  font-style: normal;
}

/* 링크서식 */
.main_nav a {
  text-decoration: none;
  color: #fff;
  background: rgb(183, 28, 255);
  padding: 5px;
}
.gnb{
  position:fixed;
  top:0px;right:-100%;
  background:rgb(0, 0, 0, .8);
  z-index:2000;
  width:100%;
  height:100vh;
  transition:0.3s ease;
}
.gnb_inner{
  list-style:none;
  text-align:left;
  padding-top:100px;
  width:50%;
  margin-left:50%;
  background:rgb(233, 162, 255, 0.5);
  height:100vh;
  line-height:35px;
}
.gnb_inner li{
  padding-left:30px;
  display:block;
  border-bottom:1px solid rgb(255, 255, 255, .5);
  color:#fff;
}
.gnb_inner li:hover{
  background:rgb(233, 162, 255, 0.8);
}
.gnb_inner button{
  border:none;
  padding-bottom: 10px;
  width:100%;
  background:none;
  color:#fff;
  font-size:18px;
}
.close_btn{
  border:none;
  position:absolute;
  right:15px;
  top:20px;
  z-index:1000;
  color:#fff;
  background:none;
  cursor: pointer;
  padding:10px;
}
/* 메인서식 */
main {
  padding: 15px 10px;
}

.list01 {
  margin: 10px 0px 15px 30px;
}

main .title2 {
  line-height: 40px;
  color: rgb(183, 28, 255);
  margin: 14px 0px;
}

main .title3 {
  display: inline-block;
  padding: 5px;
  color: rgb(255, 255, 255);
  background: #e9a2ff;
  margin: 10px;
}

main .title4 {
  line-height: 40px;
}
