    #product_list * {
      margin: 0px;
      padding: 0px;
    }

    #product_list {
      display: flex;
      width: 1200px;
      margin: 0 auto;
      justify-content: space-around;
    }

    #product_list li {
      list-style: none;
      width: 22%;
    }

    #product_list li img {
      width: 100%;
      border-radius: 10px;
    }

    #product_list li h3 {
      font-size: 16px;
      font-weight: normal;
    }

    #product_list .p_title {
      font-size: 14px;
      padding-top: 10px;
      line-height: 24px;
      display: block;
    }

    #product_list .p_price {
      color: #f00;
      font-weight: bold;
      font-size: 19px;
    }

    #product_list a {
      text-decoration: none;
      color: #333;
    }
