/* Sub_style.css */

.txt_color1 {
  color: rgb(255, 0, 212);
  background: rgb(209, 209, 209);
}

#bg_color1 {
  color: orange;
}

/* 배경이미지 */
.bg_img {
  background-image: url('../images/banner.jpg');
  width: 342px;
  height: 228px;
}

/* 버튼 서식 */
.btn {
  width: 40px;
  height: 30px;
  font-size: 20px;
  margin: 10px;
}

.num {
  color: #f00;
  font-weight: bold;
  font-size: 30px;
}

/*마켓 컬리 실습*/
.kelly {
  width: 600px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.product_info {
  position: relative;
  transform: translateX(200px);
  width: 60%;
  padding: 30px 0px;
  border: 1px solid #ccc;
}

.box_kelly {
  display: flex;
  justify-content: space-between;
  width: 80px;
  border: 1px solid #ccc;
  margin: 10px 0px;
}

.product_info>p {
  position: relative;
  right: 0px;
}

.box_kelly input[value] {
  width: 50px;
  text-align: center;
  font-size: 20px;
}

.btn_kelly {
  border: none;
  background: none;
}

/* 찜, 알림 , 장바구니 담기 서식 */
.btn_group {
  width: 100%;
  display: flex;
  justify-content: center;
}

.btn_group button {
  flex: 1;
  margin: 0 5px;
  padding: 10px;
}

.btn_group button .fa-regular {
  font-size: 20px;
}

.price_wrap {
  width: 600px;

}

.total_price {
  text-align: right;
  margin-right: 50px;
}

.btn_cart {
  width: 80%;
  border: none;
  background: #5f0080;
  color: #fff;
  border-radius: 3px;
}
