.modal{
  position:fixed;
  top:0;left:0;
  z-index:1000;
  width:100%;
  height:100vh;
  background:rgba(0,0,0,.7);
}
.m_inner{
  position:absolute;
  left:50%;top:50%;
  transform:translate(-200px, -250px);
  width:400px;
  height:500px;
  background:#fff;
}
.m_inner img{
  width:100%;
  display:block;
}
.m_inner button{
  width:100%;
  text-align:center;
  padding:20px 0px;
  border:none;
  color:#333;
  font-size:18px;
}
